.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

div {
  box-sizing: border-box;
}

.list {
  padding: 0 20px;
  text-align: left;
  border-bottom: 1px solid #999;
  position: relative;
}

/* .list button {
  position: absolute;
  top: 0;
  left: 150px;
} */

.mar-btn {
  margin-bottom: 10px;
}

.modal {
  margin: 20px 0;
  padding: 20px;
  background: #eee;
  text-align: left;
}

.modal .title {
  margin: 10px 0 30px;
  font-weight: 700;
  font-size: 18px;
  color: rgb(57, 57, 57);
}

.modal span {
  display: block;
  margin: 10px 0;
}

.cursor {
  cursor: pointer;
}

.black-nav {
  display: flex;
  background-color: black;
  width: 100%;
  color: #fff;
  padding-left: 20px;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
